.button {
  display: flex;
  align-items: center;
  color: #fff;
  background-color: rgb(35, 132, 223);

  cursor: pointer;

  padding: 10px 20px;
  
  width: 140px;
  
 margin-left: auto;
 margin-right: auto;
  margin-top: 50px;
}

.button > * + * {
  margin-left: 10px;
}

@media (max-width: 1099px) {
  .button span {
    display: none;
  }
}

