.cities_list__wrapper {
  display: flex;
}

@media (max-width: 719px) {
  .cities_list__wrapper {
    flex-direction: column;
  }
}

.cities_list__favourite {
  display: flex;
  flex-direction: column;

  width: 20%;
}

.cities_list__favourite ul,
.cities_list__main ul {
  padding: 0;
  margin: 0;
}

.cities_list__favourite ul {
  width: 100%;
}

.cities_list__favourite li {
  list-style: none;

  font-weight: 700;
  font-size: 18px;
  line-height: 1.1;

  letter-spacing: 0.03em;

  color: #000000;

  cursor: pointer;
}

.cities_list__favourite li:first-child {
  padding-bottom: 10px;
  color: red;
  border-bottom: 1px solid #ecf0f1;
}

.cities_list__favourite ul > * + * {
  margin-top: 10px;
}

.cities_list__main {
  margin-left: 50px;
  width: 100%;
  display: flex;

  color: #6b6b6b;
}
@media (max-width: 719px) {
  .cities_list__main {
    margin-left: 0;
    margin-top: 20px;
  }
}

.cities_list__main ul {
  width: 20%;
}

.cities_list__main ul > li + li {
  margin-top: 10px;
}

.cities_list__main li {
  list-style: none;

  cursor: pointer;
}

.cities_list__main > ul + ul {
  margin-left: 15px;
}

.autocomplete__wrapper {
  margin-bottom: 30px;
}

.autocomplete__link {
  text-decoration: none;
  color: #000;
}
